@font-face {
  font-family: "Futura m";
  src: url("./assets/fonts/futura-medium-bt.woff") format("woff"),
    url("./assets/fonts/futura-medium-bt.ttf") format("truetype");
}
@font-face {
  font-family: "Futura r";
  src: url("./assets/fonts/futura-book.woff") format("woff"),
    url("./assets/fonts/futura-book.ttf") format("truetype");
}
@font-face {
  font-family: "YuGothic";
  src: url("./assets/fonts/YuGothic.ttf") format("truetype");
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: hidden;
  /* height: 100%; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

