.o-container-profile {
  // background: chartreuse;
  width: 100%;
  height: 100%;
  position: relative;
  touch-action: none;
  overflow: hidden;
  display: flex;
  // transform: translate3d(0px, 0px, 0px);
  transition: all 1000ms cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
.o-container-image-profile {
  //   background: rgba(187, 187, 187, 0.822);
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.o-audio {
  align-self: flex-start;
  padding-left: 2.5rem;
}

@media only screen and (max-width: 800px) {
  .o-container-image-profile {
    // position: absolute;
    width: 100%;
  }
  .o-audio {
    align-self: center;
    padding-left: 0rem;
  }
}
